.pokemon {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(8, auto);
  align-items: center;
  grid-row-gap: 1rem;
  grid-column-gap: 3rem;

  align-self: start;

  padding: 0.5rem 1.5rem;
  margin: 0 auto 1.5rem auto;

  max-width: 900px;

  border-radius: $border-radius;
  background: inherit;
  box-shadow: $box-shadow;

  &--night {
    box-shadow: $dark-theme-box-shadow; }

  &__name {
    grid-column: span 3;
    grid-row: 1; }

  &__like {
    grid-column: 6 / 7;
    grid-row: 1; }

  &__abilities {
    grid-column: 3 / 7;
    grid-row: 2 / 3;

    justify-self: start;
    align-self: start; }

  &__types {
    grid-column: span 2;
    grid-row: 4 / 5;

    justify-self: center; }

  &__stats {
    grid-column: span 3;
    grid-row: 5 / 8;

    justify-self: center; }

  &__toggle-type {
    grid-column: span 6;
    grid-row: 7 / 8; }

  &__evolutions {
    grid-column: 4 / 7;
    grid-row: 5 / 9;

    align-self: start; }

  &__sprite {
    grid-column: span 2;
    grid-row: 2 / 4;

    justify-self: center; }

  @media screen and ( max-width: 972px ) {
    margin: 0 1rem 1.5rem 1rem; }

  @media screen and ( max-width: 720px ) {
    &__abilities {
      grid-column: 4 / 7;
      grid-row: 2 / 4;

      justify-self: center; }

    &__sprite {
      grid-column: span 3; }

    &__types {
      grid-column: span 3; } }

  @media screen and ( max-width: 530px ) {
    grid-gap: 1rem;

    &__name {
      grid-column: span 5; }

    &__abilities {
      grid-column: span 6;
      grid-row: 4 / 5;

      justify-self: center;
      align-self: center; }

    &__types {
      grid-column: span 6;
      grid-row: 3 / 4;

      justify-self: center; }

    &__stats {
      grid-column: span 6;
      grid-row: 5 / 6; }

    &__toggle-type {
      grid-row: 6 / 7; }

    &__evolutions {
      grid-column: span 6;
      grid-row: 6 / 9; }

    &__sprite {
      grid-row: 2 / 3;
      grid-column: span 6;
      justify-self: center; } } }
