.abilities {
  display: flex;
  flex-wrap: wrap;

  font-size: 1.1em;

  @media screen and ( max-width: 1024px) {
    position: static; }

  &__ability {
    display: inline-block;

    margin: 0.5rem 0.5rem;
    padding: 0.5em 0.8em;

    color: $main-color;
    background: $ability-color;

    border: 3px solid transparent;
    border-radius: $border-radius;

    box-shadow: $box-shadow; } }

