.filter-liked {
  font-size: 1.5em;
  padding: 0.5rem 1.5rem;

  color: $white-color;
  background: $prime-color;

  border: 2px solid transparent;
  border-radius: $border-radius;

  transition: all 0.1s;

  &--active {
    color: $prime-color;
    background: $white-color;
    border-color: $prime-color; } }
